import cx from 'classnames';
import { ReactElement, useState } from 'react';

export type PropsTab2ContentSection = {
  title: ReactElement;
  content: ReactElement;
};

type PropsTab2Section = {
  tabs: PropsTab2ContentSection[];
} & React.HTMLAttributes<HTMLDivElement>;

function Tab2({ className, tabs = [] }: PropsTab2Section) {
  const [activeTab, setActiveTab] = useState(0);

  const handleActiveTab = (id: number) => {
    setActiveTab(id);
  };

  return (
    <div className={cx(['tab2', className])}>
      <div className="tab2__content">
        {tabs.map((ta, i) => (
          <div key={i} className="tab2__item">
            <div className="tab2__index">{i + 1}</div>
            <h4
              className="tab2__title pointer"
              onClick={() => handleActiveTab(i)}
            >
              {ta.title}
            </h4>
            {activeTab === i ? (
              <div className="tab2__text">{ta.content}</div>
            ) : null}
          </div>
        ))}
      </div>
    </div>
  );
}

export default Tab2;
